          </RouterLink>
        </div>
        <div class="nav-body">
          <ul class="nav-btn" @click="onToggleDropdown">
            <li class="btn-header">
              <span>메뉴소개</span>
              <img src="@/assets/images/btn_angle_down.svg" alt="아코디언 열기">
            </li>
            <li class="btn-body">
              <ul>
                <li><RouterLink to="/menu">버거</RouterLink></li>
                <li><RouterLink to="/menu/salad">샐러드</RouterLink></li>
                <li><RouterLink to="/menu/dessert">디저트</RouterLink></li>
              </ul>
              <ul>
                <li><RouterLink to="/menu/side">사이드</RouterLink></li>
                <li><RouterLink to="/menu/drink">커피&음료</RouterLink></li>
              </ul>
            </li>
          </ul>
          <ul class="nav-btn" @click="onToggleDropdown">
            <li class="btn-header">
              <span>매장안내</span>
              <img src="@/assets/images/btn_angle_down.svg" alt="아코디언 열기">
            </li>
            <li class="btn-body">
              <ul>
                <li><RouterLink to="/store">매장찾기</RouterLink></li>
                <li><RouterLink to="/store/join">입점/개설 문의</RouterLink></li>
              </ul>
            </li>
          </ul>
          <ul class="nav-btn" @click="onToggleDropdown">
            <li class="btn-header">
              <span>슈몬라이브</span>
              <img src="@/assets/images/btn_angle_down.svg" alt="아코디언 열기">
            </li>
            <li class="btn-body">
              <ul>
                <li><RouterLink to="/live">슈몬 News</RouterLink></li>
                <li><RouterLink to="/live/story">슈몬 Story</RouterLink></li>
              </ul>
            </li>
          </ul>
          <ul class="nav-btn" @click="onToggleDropdown">
            <li class="btn-header">
              <span>브랜드소개</span>
              <img src="@/assets/images/btn_angle_down.svg" alt="아코디언 열기">
            </li>
            <li class="btn-body">
              <ul>
                <li><RouterLink to="/brand">브랜드컨셉</RouterLink></li>
                <li><RouterLink to="/brand/philosophy">운영철학</RouterLink></li>
                <li class="collaboration">
                  <span>Collaboration</span>
