                </li>
              </ul>
            </li>
          </ul>
          <RouterLink to="/quality">품질중심경영</RouterLink>
        </div>
      </nav>
    </div>
  </aside>
</template>

<script setup>
const headerChange = () => {
  const header = document.querySelector("header");

  if (window.scrollY > 100) {
    header.classList.add("after-scroll");
  } else {
    header.classList.remove("after-scroll");
  }
}

window.addEventListener("load", () => {
  headerChange();
});

window.addEventListener("scroll", () => {
  headerChange();
});

const onOpenAside = () => {
  const aside = document.querySelector("aside");

  if (aside.classList.contains("on")) {
    return;
  } else {
    aside.style.zIndex = "9999";
    aside.classList.add("on");
  }
}

const onCloseAside = () => {
  const aside = document.querySelector("aside");

  if (aside.classList.contains("on")) {
    aside.classList.remove("on");
    setTimeout(() => {
      aside.style.zIndex = "-9999";
    }, 750);
  } else {
    return;
  }
}

const onToggleDropdown = (e) => {
  const btns = document.querySelectorAll("aside > .aside-inner nav .nav-btn > .btn-header");

  if (e.target.closest(".nav-btn").classList.contains("on")) {
    e.target.closest(".nav-btn").classList.remove("on");
  } else {
    for (let btn of btns) {
      if (btn !== e.target) {
        btn.closest(".nav-btn").classList.remove("on");
      }
    }
    e.target.closest(".nav-btn").classList.add("on");
  }
};

</script>

<style lang="scss" scoped>
@import "@/styles/Header";
</style>
